
'use client';

import { useState } from 'react';
import Image from 'next/image';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { PlusCircle, Image as ImageIcon } from 'lucide-react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useToast } from "@/hooks/use-toast";

interface GalleryItem {
  id: string;
  title: string;
  imageUrl: string;
  imageHint: string;
  date: string;
}

const galleryFormSchema = z.object({
  title: z.string().min(3, { message: "Title must be at least 3 characters." }),
});

type GalleryFormValues = z.infer<typeof galleryFormSchema>;

const initialGalleryItems: GalleryItem[] = [
  { id: '1', title: 'Annual Sports Day', imageUrl: 'https://placehold.co/600x400.png', imageHint: 'sports children', date: '2024-03-15' },
  { id: '2', title: 'Science Fair Exhibition', imageUrl: 'https://placehold.co/600x400.png', imageHint: 'science experiment', date: '2024-04-22' },
  { id: '3', title: 'Art & Craft Workshop', imageUrl: 'https://placehold.co/600x400.png', imageHint: 'art craft', date: '2024-05-10' },
  { id: '4', title: 'Graduation Ceremony', imageUrl: 'https://placehold.co/600x400.png', imageHint: 'graduation students', date: '2024-06-01' },
  { id: '5', title: 'Cultural Fest', imageUrl: 'https://placehold.co/600x400.png', imageHint: 'cultural dance', date: '2023-11-20' },
  { id: '6', title: 'Tree Plantation Drive', imageUrl: 'https://placehold.co/600x400.png', imageHint: 'tree planting', date: '2023-09-05' },
];

export default function GalleryPage() {
  const { user } = useAuth();
  const { toast } = useToast();
  const [galleryItems, setGalleryItems] = useState<GalleryItem[]>(initialGalleryItems);
  const [showCreateModal, setShowCreateModal] = useState(false);

  const form = useForm<GalleryFormValues>({
    resolver: zodResolver(galleryFormSchema),
    defaultValues: {
      title: "",
    },
  });

  const handleCreateGallery = (values: GalleryFormValues) => {
    const newGalleryItem: GalleryItem = {
      id: Date.now().toString(),
      title: values.title,
      imageUrl: 'https://placehold.co/600x400.png', // Placeholder image
      imageHint: 'new gallery placeholder', // Placeholder hint
      date: new Date().toISOString().split('T')[0], // Current date
    };
    setGalleryItems(prevItems => [newGalleryItem, ...prevItems]);
    toast({
      title: "Gallery Created",
      description: `Gallery "${values.title}" has been successfully created.`,
    });
    setShowCreateModal(false);
    form.reset();
  };

  return (
    <div className="space-y-8">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold font-headline text-primary">School Gallery</h1>
          <p className="text-muted-foreground">Memories from school events and activities.</p>
        </div>
        {user?.role === 'admin' && (
          <Dialog open={showCreateModal} onOpenChange={setShowCreateModal}>
            <DialogTrigger asChild>
               <Button onClick={() => form.reset()}>
                <PlusCircle className="mr-2 h-4 w-4" /> Create Gallery
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
              <DialogHeader>
                <DialogTitle className="font-headline">Create New Gallery</DialogTitle>
                <DialogDescription>
                  Add a title for your new gallery. Images can be added later.
                </DialogDescription>
              </DialogHeader>
              <Form {...form}>
                <form onSubmit={form.handleSubmit(handleCreateGallery)} className="grid gap-4 py-4">
                  <FormField
                    control={form.control}
                    name="title"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Title</FormLabel>
                        <FormControl>
                          <Input placeholder="e.g., Annual Day 2024" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  {/* Image upload functionality can be added here in the future */}
                  {/* For now, we are using a placeholder image */}
                  <DialogFooter>
                    <Button type="button" variant="outline" onClick={() => setShowCreateModal(false)}>Cancel</Button>
                    <Button type="submit">Create</Button>
                  </DialogFooter>
                </form>
              </Form>
            </DialogContent>
          </Dialog>
        )}
      </div>

      {galleryItems.length === 0 ? (
        <Card className="text-center py-12">
          <CardHeader>
            <ImageIcon className="mx-auto h-16 w-16 text-muted-foreground/50" />
            <CardTitle className="font-headline mt-4">No Galleries Yet</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground">Start by creating a new gallery to share school memories.</p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {galleryItems.map((item) => (
            <Card key={item.id} className="overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 group">
              <CardHeader className="p-0 relative">
                <Image
                  src={item.imageUrl}
                  alt={item.title}
                  width={600}
                  height={400}
                  className="aspect-[3/2] w-full object-cover transition-transform duration-300 group-hover:scale-105"
                  data-ai-hint={item.imageHint}
                />
              </CardHeader>
              <CardContent className="p-4">
                <CardTitle className="font-headline text-lg mb-1">{item.title}</CardTitle>
                <p className="text-xs text-muted-foreground">{new Date(item.date).toLocaleDateString()}</p>
              </CardContent>
              <CardFooter className="p-4 pt-0">
                 <Button variant="outline" size="sm" className="w-full">View Gallery</Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
